<template>
  <!-- 로그인 Form -->
  <div>
    <div class="container pt-lg-md login">
      <div class="row justify-content-center">
        <div class="col-lg-7">
          <card
            type="secondary"
            shadow
            header-classes="bg-white pb-7"
            body-classes="px-lg-7 py-lg-7"
            class="border-0"
          >
            <template>
              <form role="form">
                <base-input
                  alternative
                  class="mb-5"
                  placeholder="ID"
                  addon-left-icon="ni ni-single-02"
                  v-model="user.id"
                >
                </base-input>
                <base-input
                  alternative
                  type="password"
                  placeholder="Password"
                  addon-left-icon="ni ni-lock-circle-open"
                  v-model="user.pwd"
                >
                </base-input>
                <div class="text-center">
                  <base-button type="primary" class="my-4" @click="login">로그인</base-button>
                </div>
              </form>
            </template>
          </card>
          <div class="row mt-5">
            <div class="col-6">
              <a href="javascript:void(0);" style="color: black; font-weight: bold" @click="findpwd"
                >비밀번호 찾기</a
              >
            </div>
            <div class="col-6">
              <a
                href=" javascript:void(0);"
                style="color: black; font-weight: bold"
                @click="moveJoin"
                >회원가입</a
              >
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  created() {},
  data() {
    return {
      user: {
        id: "",
        pwd: "",
      },
    };
  },
  methods: {
    login() {
      this.$store.dispatch("login", this.user);
      this.$router.push("/main");
    },
    findpwd() {
      this.$emit("change", 1);
    },
    moveJoin() {
      this.$emit("change", 2);
    },
  },
};
</script>

<style></style>
