<template>
  <!-- 회원정보 조회 -->
  <div>
    <div class="profile-page">
      <section class="section-profile-cover section-shaped my-0"></section>
      <div class="container">
        <card shadow class="card-profile mt--300" no-body>
          <div class="px-4">
            <div class="row justify-content-center">
              <div class="col-lg-4 order-lg-3 text-lg align-self-lg-center">
                <div class="card-profile-actions py-4 mt-lg-0">
                  <base-button type="info" size="m" class="mr-4" @click="moveUpdate"
                    >회원정보수정</base-button
                  >
                  <base-button type="default" size="m" class="float-right" @click="moveDelete"
                    >회원탈퇴</base-button
                  >
                </div>
              </div>
            </div>
            <div class="text-center mt-5">
              <h3>이름 : {{ user.name }}</h3>
              <h3>ID : {{ user.id }}</h3>
              <h3>EMAIL : {{ user.email }}</h3>
            </div>
            <div class="mt-5 py-5 border-top text-center">
              <div class="row justify-content-center">
                <div class="col-lg-9"></div>
              </div>
            </div>
          </div>
        </card>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["user"]),
  },
  methods: {
    moveUpdate() {
      this.$router.push("/mypage/update");
    },
    moveDelete() {
      this.$router.push("/mypage/delete");
    },
  },
};
</script>

